import Composition from '../data/Glycan';

export default class NetworkBuilder {

  static buildJsonComposition(glycanMap){
    let json = {};
    json.links = [];

    //Node Generation
    json.nodes = Object.keys(glycanMap).map(
        function(key) {
          let sugar = glycanMap[key];
          return {
            name: sugar.composition.toStringNoZeros(),
            quantification: sugar.quantification,
            size: sugar.composition.calculateSize(),
            unicarbEncoding: sugar.composition.toUnicarbLongEncoding(),
            hexNAc: sugar.composition.HexNAc
          };});

    //Edge Generation
    let i = 0;
    for (let key in glycanMap) {
      const size = glycanMap[key].composition.calculateSize();
      let j = 0;
      for(let innerKey in glycanMap){
        if(size + 1 == glycanMap[innerKey].composition.calculateSize() && this._cumulativeDifference(glycanMap[innerKey].composition,glycanMap[key].composition) ){
          let link = {
            source: i,
            target: j,
            difference: this._differenceMonosaccharide(glycanMap[innerKey].composition,glycanMap[key].composition),
            value: 1
          };
          json.links.push(link);
        }
        j++;
      }
      i++;
    }
    return json;
  }


  static _differenceMonosaccharide(a,b){

    if(a.Fucose != b.Fucose){
      return 'Fuc';
    }
    if(a.Hex != b.Hex){
      return 'Hex';
    }
    if(a.HexNAc != b.HexNAc){
      return 'HexNAc';
    }
    if(a.NeuAc != b.NeuAc){
      return 'NeuAc';
    }
    if(a.NeuGc != b.NeuGc){
      return 'NeuGc';
    }
    if(a.Sulf != b.Sulf){
      return 'Sulf';
    }
    return "";
  }

  static _cumulativeDifference(a,b){

    let totalDiff = 0;
    if(a.Fucose != b.Fucose){
      totalDiff++;
    }
    if(a.Hex != b.Hex){
      totalDiff++;
    }
    if(a.HexNAc != b.HexNAc){
      totalDiff++;
    }
    if(a.NeuAc != b.NeuAc){
      totalDiff++;
    }
    if(a.NeuGc != b.NeuGc){
      totalDiff++;
    }
    if(a.Sulf != b.Sulf){
      totalDiff++;
    }

    return totalDiff;
  }
}


